.three-wrap {
    height: 100vh;
    width: 100vw;

    button {
        position: fixed;
        bottom: 50px;
        bottom: calc(env(safe-area-inset-bottom) / 2 + 50px);
        left: 50%;
        transform: translateX(-50%) translateY(100px);
        box-shadow: 3px 3px 20px 5px rgba(0,0,0,.2);
opacity: 0;

        animation: .3s 1.5s buttonMotion ease-out forwards;

        @keyframes buttonMotion {
            0% {
opacity: 0;
transform: translateX(-50%) translateY(100px);
}
100% {
    opacity: 1;
    transform: translateX(-50%) translateY(0);

            }
            
        }
    }
}