@import url(https://fonts.googleapis.com/css?family=Noto+Sans|Noto+Sans+KR);
.three-wrap{height:100vh;width:100vw}.three-wrap button{position:fixed;bottom:50px;bottom:calc(env(safe-area-inset-bottom) / 2 + 50px);left:50%;transform:translateX(-50%) translateY(100px);box-shadow:3px 3px 20px 5px rgba(0,0,0,0.2);opacity:0;animation:.3s 1.5s buttonMotion ease-out forwards}@keyframes buttonMotion{0%{opacity:0;transform:translateX(-50%) translateY(100px)}100%{opacity:1;transform:translateX(-50%) translateY(0)}}

.page-template{position:relative;display:flex;flex-direction:column;height:100%}

#room{display:flex;justify-content:center;align-items:center}

#setting{display:flex;justify-content:center;align-items:center;height:100vh;font-size:50px}

.color-picker{display:flex;justify-content:center;align-items:center;color:black;font-size:11px}.color-picker>input{text-align:center;width:30px;height:30px;border:1px solid #e6e6e6;background-color:white}

.tool-size-selector{display:flex;justify-content:center;align-items:center;color:black;font-size:11px}.tool-size-selector>input{text-align:center;width:30px;height:30px;border:1px solid #e6e6e6;background-color:white}.tool-size-selector>button{width:30px;height:30px;background-color:white;border:1px solid #e6e6e6;border-radius:50%}

#practice-room{display:flex;justify-content:center;align-items:center;height:100vh;font-size:50px}#practice-room canvas{background-color:white}

*{margin:0;padding:0;box-sizing:border-box;border:0;font-family:system-ui, 'AppleSDGothicNeo', sans-serif;font-style:normal;word-wrap:break-word;word-break:keep-all;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto}*:focus{outline:none}input:disabled{background-color:#ebebe4}button{cursor:pointer;border:0;background:none}body{line-height:1.6;font-size:16px;font-family:'Noto Sans KR', 'Noto Sans', 'Malgun Gothic', sans-serif;color:#222;background:#3068cc}

